import classes from './../Friends.module.css';
import userPic from '../../../assets/images/user.png'
import Preloader from '../../Common/Preloader';



const Friend = ({ followedUsers }) => {
    
    if (!followedUsers) {
        return <Preloader/>
    }
    let followed = followedUsers.reverse().filter(u => followedUsers.indexOf(u) < 3);
    return (
            <div className={classes.friendItems}>
            {followed.map(u => <div key={u.name} className={classes.friendItem} ><div>{u.name}</div>
                    <img src={u.photos.small || userPic}></img></div>
            )}
            </div>
    )
}



export default Friend;

